import { Injectable } from '@angular/core';
import { TenantUiService } from '@c8y/ngx-components';
import { SigfoxDeviceRegistrationButtonComponent } from './sigfox-device-registration-button.component';
import { RegisterDeviceFactory, RegisterDeviceItem } from '@c8y/ngx-components/register-device';
@Injectable()
export class SigfoxDeviceRegistrationFactory implements RegisterDeviceFactory {
  constructor(private tenantService: TenantUiService) {}

  get() {
    const items: RegisterDeviceItem[] = [];
    if (this.tenantService.isMicroserviceSubscribedInCurrentTenant('sigfox-agent')) {
      items.push({
        template: SigfoxDeviceRegistrationButtonComponent,
        priority: 98,
        category: 'single'
      } as RegisterDeviceItem);
    }
    return items;
  }
}
