import { NgModule } from '@angular/core';
import { CommonModule, CoreModule } from '@c8y/ngx-components';
import { HOOK_DEVICE_REGISTRATION } from '@c8y/ngx-components/register-device';
import { SigfoxDeviceRegistrationButtonComponent } from './sigfox-device-registration-button.component';
import { SigfoxDeviceRegistrationComponent } from './sigfox-device-registration.component';
import { SigfoxDeviceRegistrationFactory } from './sigfox-device-registration.factory';
import { SigfoxProviderService } from './sigfox-provider.service';

@NgModule({
  imports: [CoreModule, CommonModule],
  declarations: [SigfoxDeviceRegistrationButtonComponent, SigfoxDeviceRegistrationComponent],
  providers: [
    {
      provide: HOOK_DEVICE_REGISTRATION,
      useClass: SigfoxDeviceRegistrationFactory,
      multi: true
    },
    SigfoxProviderService
  ]
})
export class SigfoxDeviceRegistrationModule {}
