import { Injectable } from '@angular/core';
import {
  ApplicationService,
  FetchClient,
  IFetchOptions,
  IManagedObject,
  InventoryService,
  IResult,
  IResultList,
  ITenantOption,
  TenantOptionsService
} from '@c8y/client';
import { SigfoxDevice } from './sigfox-device-registration.model';
import { TranslateService } from '@ngx-translate/core';
import { gettext } from '@c8y/ngx-components';

export enum ErrorName {
  NoDeviceProtocolsError = 'NoDeviceProtocolsError',
  NoConnectivitySettingsError = 'NoConnectivitySettingsError',
  ContractError = 'ContractError',
  NoContractsError = 'NoContractsError',
  RegistrationError = 'RegistrationError',
  DeviceProtocolsFetchError = 'DeviceProtocolsFetchError'
}

@Injectable({
  providedIn: 'root'
})
export class SigfoxProviderService {
  private readonly baseUrl: string = '/service/sigfox-agent/';
  private readonly registrationUrl: string = `${this.baseUrl}newDeviceRequest`;
  private readonly contractsUrl: string = `${this.baseUrl}contract`;
  private readonly header: any = { 'Content-Type': 'application/json' };

  constructor(
    private client: FetchClient,
    private inventoryService: InventoryService,
    private tenantOptions: TenantOptionsService,
    private translateService: TranslateService,
    private applicationService: ApplicationService
  ) {}
  async getConnections() {
    const options: IFetchOptions = {
      method: 'GET',
      headers: this.header
    };
    const res = await this.client.fetch(`${this.baseUrl}lns-connection`, options);
    const data = await res.json();

    if (res.status === 200) {
      if (data.length === 0) {
      await this.throwNoConnectivitySettingsError();
      }
    } else {
      await this.throwNoConnectivitySettingsError();
    }
    return { res, data };
  }

  /**
   * Gets contracts from Sigfox platform.
   * @param connectionName The name of connection for which contracts will be retrieved
   * @returns The result list with contract, or throws an error with exception.
   */
  async getContracts(connectionName: string): Promise<IResultList<any>> {
    const options: IFetchOptions = {
      method: 'GET',
      headers: this.header,
      params: {
        sigfoxConnectionName: connectionName
      }
    };
    const res = await this.client.fetch(this.contractsUrl, options);

    const data = await res.json();

    if (res.status === 200) {
      if (data.length === 0) {
        this.throwNoContractsError();
      }
    } else {
      this.throwContractError(data);
    }
    return { res, data };
  }

  async createDevice(device: SigfoxDevice): Promise<IResult<SigfoxDevice>> {
    const options: IFetchOptions = {
      method: 'POST',
      headers: this.header,
      body: JSON.stringify(device)
    };
    const res = await this.client.fetch(this.registrationUrl, options);
    const data = await res.json();

    if (res.status !== 201) {
      this.throwRegistrationError(data);
    }
    return { res, data };
  }

  async getAvailableProtocols(
    filter: object = { withTotalPages: true }
  ): Promise<IResultList<IManagedObject>> {
    const query = {
      __filter: {
        __and: [
          { __has: 'c8y_IsDeviceType' },
          {
            type: { __in: ['c8y_SigfoxDeviceType', 'c8y_LpwanDeviceType'] }
          }
        ]
      },
      __orderby: [{ name: 1 }]
    };
    const { res, data } = await this.inventoryService.listQuery(query, filter);

    if (res.status === 200) {
      if (data.length === 0) {
        this.throwNoDeviceProtocolsError();
      }
    } else {
      this.throwDeviceProtocolsFetchError();
    }

    return { res, data };
  }

  async hasConnectivitySettings(): Promise<boolean> {
    const option: ITenantOption = {
      category: 'sigfox-agent',
      key: 'provider.token'
    };
    try {
      await this.tenantOptions.detail(option);
      return true;
    } catch (e) {
      await this.throwNoConnectivitySettingsError();
    }
  }

  private async throwNoConnectivitySettingsError() {
    const error = new Error();
    error.name = ErrorName.NoConnectivitySettingsError;
    const hasAdminRight = (await this.applicationService.isAvailable('administration')).data;
    if (hasAdminRight) {
      error.message = this.translateService.instant(
        gettext(
          `Connectivity settings are not configured. Configure them in the Administration app under <a href="{{ link }}">Settings</a>.`
        ),
        {
          link: '/apps/administration/index.html#/connectivitySettings/sigfox_provider_settings'
        }
      );
    } else {
      error.message = gettext(
        'Connectivity settings are not configured. Contact the administrator.'
      );
    }

    throw error;
  }

  private throwRegistrationError(data: { message: string }) {
    const error = new Error();
    error.name = ErrorName.RegistrationError;
    error.message = data.message;
    throw error;
  }

  private throwDeviceProtocolsFetchError() {
    const error = new Error();
    error.name = ErrorName.DeviceProtocolsFetchError;
    error.message = gettext('Could not load device protocols.');
    throw error;
  }

  private throwNoDeviceProtocolsError() {
    const error = new Error();
    error.name = ErrorName.NoDeviceProtocolsError;
    error.message = this.translateService.instant(
      gettext(
        `No device protocols configured. Create a Sigfox device protocol in <a href="{{ link }}">Device protocols</a>.`
      ),
      {
        link: '/apps/devicemanagement/#/deviceprotocols'
      }
    );
    throw error;
  }

  private throwContractError(data: { message: string }) {
    const error = new Error();
    error.name = ErrorName.ContractError;
    error.message = data.message;
    throw error;
  }

  private throwNoContractsError() {
    const error = new Error();
    error.name = ErrorName.NoContractsError;
    error.message = gettext(
      'No contracts found. New contracts must be created via the Sigfox platform.'
    );
    throw error;
  }
}
