import { ChangeDetectorRef, Component } from '@angular/core';
import { FormlyFieldConfig } from '@ngx-formly/core';
import { gettext, C8yStepper } from '@c8y/ngx-components';
import { AbstractControl, FormGroup } from '@angular/forms';
import { BehaviorSubject, defer, forkJoin, from, of, Subject, throwError } from 'rxjs';
import { CdkStep } from '@angular/cdk/stepper';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { SigfoxProviderService, ErrorName } from './sigfox-provider.service';
import {
  catchError,
  map,
  switchMap,
  shareReplay,
  takeUntil,
  mergeMap
} from 'rxjs/operators';
import { cloneDeep, uniq } from 'lodash-es';
import { SigfoxDevice, SigfoxDeviceFormly } from './sigfox-device-registration.model';
import { TranslateService } from '@ngx-translate/core';

type State =
  | 'loadPending'
  | 'loadSuccess'
  | 'loadError'
  | 'registrationPending'
  | 'registrationSuccess'
  | 'registrationError';

@Component({
  selector: 'c8y-sigfox-device-registration',
  templateUrl: 'sigfox-device-registration.component.html'
})
export class SigfoxDeviceRegistrationComponent {
  stepper: C8yStepper;
  readonly PAGING: object = {
    withTotalPages: true,
    pageSize: 10
  };

  form = new FormGroup({});
  model: SigfoxDeviceFormly = {} as any;
  protocols$ = this.getProtocols$();
  connections$ = this.getConnections$();
  unsubscribe$: Subject<void> = new Subject();

  load$ = this.connections$.pipe(
    catchError((error: Error) => of(error)),
    switchMap(connections => {
      if (connections instanceof Error && connections.name === ErrorName.NoConnectivitySettingsError) {
        return of([connections]);
      }
      return forkJoin([
        of(connections),
        this.protocols$.pipe(catchError((error: Error) => of(error)))
      ]);
    }),
    map(results => {
      return results.filter(result => {
        return result instanceof Error;
      });
    }),
    switchMap(errors => {
      return errors.length === 0 ? of([]) : throwError(errors);
    })
  );

  fields: FormlyFieldConfig[] = [
    {
      key: 'id',
      type: 'string',
      templateOptions: {
        placeholder: 'FED987',
        label: gettext('ID'),
        required: true,
        pattern: '(0x){0,1}[0-9A-F]+(h){0,1}'
      },
      validation: {
        messages: {
          pattern: gettext('Must be a valid hexadecimal number.')
        }
      }
    },
    {
      key: 'pac',
      type: 'string',
      templateOptions: {
        placeholder: 'FEDCBA9876543210',
        label: gettext('PAC'),
        required: true,
        pattern: '^([a-fA-F0-9]{16})$'
      },
      validation: {
        messages: {
          pattern: gettext('Must be a valid 16 digit hexadecimal number.')
        }
      }
    },
    {
      key: 'connection',
      type: 'typeahead',
      templateOptions: {
        label: gettext('Connection'),
        required: true,
        c8yForOptions: this.connections$,
        displayProperty: 'name',
        valueProperties: ['name']
      }
    },
    {
      key: 'contract',
      type: 'typeahead',
      templateOptions: {
        label: gettext('Contract'),
        required: true,
        placeholder: 'Free contract_25',
        displayProperty: 'name',
        valueProperties: ['id'],
        description: gettext('Only active contracts with free slots are displayed.')
      },
      hooks: {
        onInit: field => {
          const connectionControl = field.form.get('connection');
          connectionControl.valueChanges
          .pipe(
            takeUntil(this.unsubscribe$),
            mergeMap(() => this.getContracts$(this.form.get('connection').value.name))
          )
          .subscribe(
            profiles => {
              field.templateOptions.c8yForOptions = of(profiles);
              field.formControl.setValue(null);
            },
            error => {
              field.form.controls.contract.setErrors({ contract: true });
              field.validators.contract.message = error.message;
            }
          );
        }
      },
      validators: {
        contract: {
          expression: (control: AbstractControl) => {
            return control.status === 'VALID';
          },
          message: () => '',
        },
      }
    },
    {
      key: 'deviceType',
      type: 'typeahead',
      templateOptions: {
        label: gettext('Device protocol'),
        required: true,
        c8yForOptions: this.protocols$,
        displayProperty: 'name',
        valueProperties: ['id', 'name']
      }
    },
    {
      key: 'productCertificate',
      type: 'string',
      templateOptions: {
        placeholder: 'P_001F_EDCB_01',
        label: gettext('Product certificate key'),
        pattern: 'P_[0-9A-F]{4}_[0-9A-F]{4}_[0-9A-F]{2}',
        description: gettext(
          'If no product certificate key is specified, the device is considered a prototype.'
        )
      },
      validation: {
        messages: {
          pattern: (error, field: FormlyFieldConfig) =>
            this.translateService.instant(
              gettext('Must be a valid product certificate key, for example, {{ example }}'),
              { example: 'P_001F_EDCB_01' }
            )
        }
      }
    }
  ];

  registrationStepLabels = {
    next: gettext('Register')
  };
  finalStepLabels = {
    back: gettext('Close')
  };

  state: State = 'loadPending';
  errors$ = new BehaviorSubject<Error[]>([]);
  errorMessages$ = this.errors$.pipe(
    map(errors => errors.map(error => error.message)),
    map(messages => uniq(messages))
  );
  constructor(
    public bsModalRef: BsModalRef,
    private sigfoxService: SigfoxProviderService,
    private translateService: TranslateService
  ) {
    this.load$.subscribe(
      () => {
        this.state = 'loadSuccess';
      },
      errors => {
        this.state = 'loadError';
        this.errors$.next(errors);
      }
    );
  }

  async create(event: { stepper: C8yStepper; step: CdkStep }) {
    this.state = 'registrationPending';
    const sigfoxDevice = this.getSigfoxDeviceToSend();
    try {
      await this.sigfoxService.createDevice(sigfoxDevice);
      this.state = 'registrationSuccess';
    } catch (error) {
      this.state = 'registrationError';
      this.errors$.next([error]);
    }

    event.stepper.next();
  }

  getSigfoxDeviceToSend() {
    const sigfoxDevice: SigfoxDevice = cloneDeep(this.model);
    sigfoxDevice.lnsConnectionName = this.model.connection.name;
    sigfoxDevice.contractId = this.model.contract.id;
    sigfoxDevice.prototype = !sigfoxDevice.productCertificate;
    delete (sigfoxDevice as any).contract;
    delete (sigfoxDevice as any).connection;
    return sigfoxDevice;
  }

  getContracts$(name) {
    return defer(() => from(this.sigfoxService.getContracts(name))).pipe(shareReplay(1));
  }

  getProtocols$() {
    return defer(() => from(this.sigfoxService.getAvailableProtocols())).pipe(shareReplay(1));
  }

  getConnections$() {
    return defer(() => from(this.sigfoxService.getConnections())).pipe(shareReplay(1));
  }

  ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }
}
